.header {
  display         : flex;
  justify-content : space-between;
  align-items     : center;
  width           : 100%;
  height          : 72px;
  padding         : 8px 24px 8px 16px;
  background-color: $color-grey;
  box-shadow      : 0px 4px 10px 0px rgba(0, 0, 0, 0.15);

  &__chips {
    &--unit {
      border       : solid 1px black;
      padding      : 8px 26px;
      border-radius: 20px;
      margin-left  : 12px;
      font-family  : $font-normal;
      font-size    : 12px;
      line-height  : 16px;
  
      &:hover {
        color           : $color-white;
        border          : none;
        background-color: $color-btn;
      }
  }
  }

  &__filter {
    &--btn {
      display         : flex;
      justify-content : center;
      align-content   : center;
      width           : 92px;
      height          : 40px;
      padding         : 12px 16px;
      border-radius   : 5px;
      font-family     : $font-normal;
      font-size       : 14px;
      line-height     : 16px;
      font-weight     : 500;
      background-color: $color-btn;
      color           : $color-white;
    }
  }
}